function decimalAdjust(type, value, exp) {
    if (typeof exp === 'undefined' || +exp === 0) {
      return Math[type](value);
    }
    value = +value;
    exp = +exp;
    if (isNaN(value) || !(typeof exp === 'number' && exp % 1 === 0)) {
      return NaN;
    }
    value = value.toString().split('e');
    value = Math[type](+(value[0] + 'e' + (value[1] ? (+value[1] - exp) : -exp)));
    value = value.toString().split('e');
    return +(value[0] + 'e' + (value[1] ? (+value[1] + exp) : exp));
}

if (!Math.round10) {
	Math.round10 = function(value, exp) {
	  return decimalAdjust('round', value, exp);
	};
}


//${/*(parseInt(price)===4) ? "150" : Math.round10(parseInt(price*c), 1)+10*/}
function makeList(n,c,p){
	db.getInventory('Closer.RecordStore', {sort:'listed', sort_order:'desc', "page": p, "per_page": n}, function(err, data){
		var lst=data.listings.reverse();
		lst.forEach(function(element, index){
			console.log(element);
			var _id=element.release.id, price=element.price.value, conditioncomment=element.comments;
			setTimeout(function(){
				var dbr = dis.database();
				console.log(' '+(index+1)+' ');
				dbr.getRelease(_id, function(_err, _data){
				    output+=`<div class="release-card">
						<div class="release-name-complex">`;
					if(_data!=null && typeof(_data)!="undefined"){
						if(_data.labels!=null && typeof(_data.labels)!="undefined"){
							if(_data.labels[0].thumbnail_url.length>10) output+='<img src="'+_data.labels[0].thumbnail_url+'" />';
						}
						//<div class="release-qr" data-qrdata="${_data.id}"></div>
						output+=`<div class="release-name-complex-text">
									<div class="release-name">
										<span class="release-artist">${_data.artists_sort}</span>  
										<span class="release-title">${_data.title}</span>
									</div>
									<div class="release-details">
										<span class="release-year">${_data.year}</span>, 
										<span class="release-country">${_data.country}</span>,
										<span class="release-country">${_data.labels[0].name}</span>
									</div>
								</div>
							</div>


							<footer class="release-footer">
								<div class="release-genres">
									${(!_data.styles) ? _data.genres.join(', ') : _data.styles.join(', ')}
									${(conditioncomment) ? ('<i>('+conditioncomment+')</i>') : ''}
								</div>
								<div class="release-price">
									${price} <small>EUR</small>	
								</div>
							</footer>
							<div class="release-card-action">
								<a href="" class="item-remove" onclick="event.preventDefault();removeItem(this);"><i></i></a>
								<a href="" class="item-duplicate" onclick="event.preventDefault();duplicateItem(this);"><i></i></a>
							</div>
					    </div>`;

					    if(index===n-1){
					    	document.getElementById('root').innerHTML=output;
					    	document.getElementById('scene').classList.add("third-step");
					    	document.getElementById('scene').addEventListener("transitionend", function(){
					    		document.getElementById('scene').classList.add("final");
					    	}, {once: true})

					    	/*var releases = document.querySelectorAll('.release-qr');
					    	releases.forEach(element => {
					    		var qrData=element.getAttribute('data-qrdata');
						    	ReactDOM.render(
								  <QRCode
								    value={qrData}
								    size={64}
								    bgColor={"#ffffff"}
								    fgColor={"#000000"}
								    level={"M"}
								    includeMargin={false}
								    renderAs={"svg"}
								  />,
								  element  
								);
					    	});*/
					    }

					}
				});
			}, index*2600)
		})
	});
}

