window.removeItem = (elem) => {
    //console.log('Button clicked');
    elem.closest(".release-card").remove();
};
window.duplicateItem = (elem) => {
    var cln=elem.closest(".release-card").cloneNode(true);
    elem.closest(".release-card").after(cln);
};


document.getElementById('form').addEventListener("submit",function(event){
	event.preventDefault();
	var num=parseInt(document.getElementById('NMB').value),
		cur=parseInt(document.getElementById('CUR').value),
		pg=parseInt(document.getElementById('PG').value);
	document.getElementById('scene').classList.add("second-step");
	makeList(num,cur,pg);
});


